@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Ubuntu';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bgme {
  background-color: black;
}

.shadow {
  box-shadow: 1px 1px 20px -4px rgba(205, 191, 155, 0.5);
  -webkit-box-shadow: 1px 1px 20px -4px rgba(205,191,155,.5);
  -moz-box-shadow: 1px 1px 20px -4px rgba(205,191,155,.5);
}

svg {
  cursor: pointer;
}

path {
  fill: #B5A181;
}

path:hover {
  fill: #ebd1a9;
}

.chat {
  height: calc(100% - 160px);
  width: calc(100%);
  padding-left: 50px;
  padding-right: 50px;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

::-webkit-scrollbar-track {
  background-color: #b5a18128;
}

::-webkit-scrollbar-thumb {
  background: #B5A181;
}

::-webkit-scrollbar-thumb:hover {
  background: #ebd1a9; 
}